<template>
  <div class="d-flex justify-content-between">
    <div class="list-row d-flex mb-3">
      <h1>{{ cliente.value }}</h1>
    </div>

    <div class="list-row d-flex mb-3">
      <h1>{{ fecha.value }}</h1>
    </div>
  </div>

  <div class="col-md-12">
    <a
      class="list-group-item list-group-item-action list-group-item-light d-flex justify-content-between flex-column"
      v-for="item in pedidox"
      :key="item"
    >
      <div class="d-flex justify-content-between align-items-center">
        <span>Cantidad: x{{ item.cantidad }}</span>
        <span>
          <h5>{{ item.nombre }}</h5>
        </span>
        <span>Unidad: ${{ item.precioU }}</span>
        <span>Total: ${{ item.precioT }}</span>
      </div>

      <div class="d-flex justify-content-between"></div>
    </a>
  </div>

  <h1 class="d-flex justify-content-center my-5">TOTAL: ${{ total }}</h1>
</template>

<script>
import { reactive, computed, onMounted } from "vue";
import { useRoute } from "vue-router";
import { getPedido } from "@/firebase";

export default {
  setup() {
    const route = useRoute();
    const pedidoId = computed(() => route.params.id);
    const pedidox = reactive([]);
    const cliente = reactive({});
    const fecha = reactive({});

    onMounted(async () => {
      const pedido = await getPedido(pedidoId.value);
      for (const x in pedido) {
        pedidox.push(pedido[x]);
      }
      cliente.value = pedidox.pop()
      fecha.value = pedidox.pop()
    });

    return { pedidox, cliente, fecha };
  },
  computed: {
    total: function () {
      var total = 0;
      for (var x in this.pedidox) {
        total = total + this.pedidox[x].precioT;
      }
      return total;
    },
  },
};
</script>